.wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 2rem;
}

.label {
  font-weight: bold;
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 0.5rem;
}

.labelSubtext {
  font-weight: normal;
  font-size: 0.875rem;
  margin-top: 0.5rem;
}

.requiredMarker {
  color: red;
}

.selectedItem {
  background: white;
  border: 1px solid grey;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
  padding: 0.75rem;
  padding-right: 50px;
  position: relative;
  cursor: pointer;
}

.selectedItem:hover .selectButton {
  background-color: #303030;
}

.selectButton {
  background-color: #404040;
  width: 50px;
  height: 100%;
  color: white;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  right: 0;
  top: 0;
  position: absolute;
}

.selectButtonIcon {
  width: 2.4rem;
  height: 2.4rem;
}

.menu {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-y: scroll;
  background-color: #eee;
  list-style: none;
  max-height: 5.5rem;
  position: absolute;
  top: 100%;
  z-index: 1;
}

.selectItem {
  font-size: 0.875rem;
  padding: 0.25rem 0.75rem;
}

.selectItem.highlighted {
  background-color: #bde4ff;
}
.subTextWithLink {
  font-style: italic;
  font-weight: normal;
}
.subTextWithLinkHidden {
  font-style: italic;
  font-weight: normal;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;  
  overflow: hidden;
}
.moreInfo {
  background: none!important;
  border: none;
  padding: 0!important;
  font-family: arial, sans-serif;
  color: #069;
  text-decoration: underline;
  cursor: pointer;
  align-items: flex-start;
  width: 75px;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #3e3ef2;
}
