.button {
  border: none;
  background-color: #10448c;
  font-weight: bold;
  line-height: 28px;
  color: #fff;
  padding: 0.5rem 1.25rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  vertical-align: top;
  transition: background-color 0.2s;
  cursor: pointer;
}

.button:hover {
  background-color: #002f67;
}

.button:focus {
  outline: 3px solid #ffd617;
  outline-offset: 1px;
}

.button.outline {
  background-color: #fff;
  box-shadow: inset 0 0 0 3px #10448c;
  color: #10448c;
  transition: color 0.2s, box-shadow 0.2s;
}

.button.outline:hover {
  color: #002f67;
  box-shadow: inset 0 0 0 3px #002f67;
}

.button.hasIconPrevious {
  padding-left: 0.75rem;
}

.button.hasIconNext {
  padding-right: 0.75rem;
}

.icon {
  width: 28px;
  height: 28px;
}

.icon.iconPrevious {
  margin-right: 0.25rem;
}

.icon.iconNext {
  margin-left: 0.25rem;
}
