.pageheader__wrapper {
  background-color: #004494;
  margin: 0;
  padding-bottom: 2rem;
}

.pageheader__container {
  padding-top: 0;
  padding-bottom: 0;
}

.breadcrumbs {
  border-bottom: 1px solid #fff;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0 0 calc(1rem - 1px);
}

.crumb {
  align-items: center;
  display: inline-flex;
  font: normal normal 400 0.875rem/1rem Arial, sans-serif;
  margin-top: 1rem;
  max-width: 100%;
}

.crumb > a {
  color: #fff;
  font-weight: 600;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-decoration: none;
}

.crumb > a:hover {
  text-decoration: underline;
}

.crumb > a:focus {
  outline: 3px solid #ffd617;
  outline-offset: 2px;
}

.crumb > svg {
  stroke: #fff;
  width: 16px;
  height: 16px;
  margin: 0 8px;
}

.crumb:last-of-type > a {
  color: #bfd0e4;
  font-weight: 600;
}

.title {
  color: #fff;
  font: normal normal 600 1.75rem/2rem Arial, sans-serif;
  font-weight: 600;
  margin-bottom: 0;
  margin-top: 2rem;
}

.subtitle {
  color: #fff;
  font: normal normal 400 1.125rem/1.75rem Arial, sans-serif;
  margin-bottom: 0;
  margin-top: 1rem;
  max-width: 80ch;
}

@media (min-width: 996px) {
  .title {
    font: normal normal 600 2rem/2.5rem Arial, sans-serif;
  }

  .subtitle {
    font: normal normal 400 1.25rem/1.75rem Arial, sans-serif;
  }
}
