.fieldset {
  border: none;
  padding: 0;
  margin: 0;
}

.legend {
  font-weight: bold;
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 0.5rem;
}

.radioLabel {
  padding: 0.75rem 2.5rem;
  font-weight: bold;
  border: 1px solid grey;
  text-align: center;
  display: inline-flex;
  justify-content: center;
  position: relative;
  cursor: pointer;
}

.radioInput:checked + .radioLabel {
  background-color: #10448c;
  color: #fff;
}

.radioLabel:not(:last-of-type) {
  margin-right: 2rem;
}

.radioInput {
  visibility: hidden;
  position: absolute;
}

.labelSubtext {
  font-weight: normal;
  font-size: 0.875rem;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}
