.container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.logo {
  height: 1.75rem;
  width: 7rem;
}

.actions {
  display: flex;
  flex-grow: 1;
}

.search {
  display: flex;
  flex-grow: 1;
  max-width: 100%;
  align-items: center;
}

.search__label {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  visibility: visible;
  white-space: nowrap;
  width: 1px;
}

.search__input {
  appearance: none;
  margin: 0;
  box-shadow: none;
  box-sizing: border-box;
  color: #404040;
  font: normal normal 400 1rem/1.25rem Arial, sans-serif;
  padding: calc(0.75rem - 1px);
  border: 1px solid #9f9f9f;
  border-right-width: 0;
  width: 100%;
}

.search__input:hover {
  outline: 1px solid #004494;
  outline-offset: -1px;
}

.logo__link:focus,
.logo__link:active,
.search__input:focus,
.search__input:active,
.search__button:focus {
  outline: 3px solid #ffd617;
  outline-offset: -3px;
}

.search__button {
  border-bottom: 1px solid #9f9f9f;
  border-left-width: 0;
  border-right: 1px solid #9f9f9f;
  border-top: 1px solid #9f9f9f;
  padding: calc(0.75rem - 1px);

  background-color: #ebebeb;
  color: #004494;
  font: normal normal 600 1rem/1.25rem Arial, sans-serif;
}

.search__button:active,
.search__button:hover {
  background-color: #cfcfcf;
}

@media (min-width: 480px) {
  .logo {
    height: 3rem;
    width: 12.1rem;
  }
}

@media (min-width: 996px) {
  .logo {
    height: 3.75rem;
    width: 15.1rem;
  }
}

@media (min-width: 1140px) {
  .logo {
    height: 4.5rem;
    width: 18rem;
  }
}
