@import url('./styles/normalize.css');

html,
body {
  font-size: 16px;
}

.title-underline {
  border-bottom: 1px solid black;
  padding-bottom: 0.5rem;
  margin-bottom: 2rem;
  display: block;
}

.fw {
  width: 100%;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;
  padding: 2rem 1rem;
}

.question-wrapper {
  margin-bottom: 2rem;
}

.form-controls > button {
  margin-right: 2rem;
}

.results-information-container {
  background-color: #f5f5f5;
  padding: 2rem 1rem;
  margin: 2rem 0;
  display: flex;
  flex-direction: column;
}

.results-information-slider-title {
  font-size: 1.2rem;
}

.results-information-text-title {
  margin-bottom: 1rem;
}

.results-information-section {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.results-information-section > div {
  margin-bottom: 2rem;
}

.progress-slider {
  height: 2rem;
  position: relative;
  width: 100%;
  margin-bottom: 1rem;
}

.progress-slider::before {
  height: 1px;
  position: absolute;
  width: 100%;
  background-color: black;
  content: " ";
  top: 50%;
  left: 0;
}

.progress-slider::after {
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 8px;
  background-color: white;
  content: " ";
  border: 1px solid black;
  top: 50%;
  left: var(--value);
  transform: translateY(-50%);
}

.progress-pill {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.progress-pill > div {
  border: 1px solid black;
  background-color: white;
  height: 2rem;
  flex-grow: 1;
  margin-right: 0.5rem;
}

.progress-pill > div:first-of-type {
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
}

.progress-pill > div:last-of-type {
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
  margin-right: 0;
}

@media screen and (min-width: 640px) {
  .question-wrapper {
    max-width: 540px;
  }
}

@media screen and (min-width: 768px) {
  .results-information-container {
    padding: 2rem;
    margin: 2rem 0;
    flex-direction: row;
    align-items: center;
  }

  .results-information-section {
    padding: 4rem;
  }

  .results-information-section:first-of-type {
    border-right: 1px solid black;
  }
}

.sub-title {
  margin-top: 10px;
  margin-bottom: 50px;
}
