.footer {
  background-color: #004494;
}

.footer hr {
  width: 100%;
  height: 1px;
  border-bottom: 1px solid #7fa1c9;
  margin-bottom: 2rem;
}

.footerContainer {
  display: flex;
  flex-direction: column !important;
}

.row {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  flex-direction: column;
}

.menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.menu li {
  margin-bottom: 1rem;
}

.footer a {
  color: #fff;
  text-decoration: none;
  font: normal normal 400 0.875rem/1.25rem Arial, sans-serif;
}

.footer a:focus {
  outline: 3px solid #ffd617;
  outline-offset: 1px;
}

.footer a:hover {
  text-decoration: underline;
}

.footer a.bold {
  font-weight: bold;
  font-size: 1rem;
}

.description {
  color: #bfd0e4;
  margin-top: 2rem;
  font: normal normal 400 0.875rem/1.25rem Arial, sans-serif;
}

@media (min-width: 768px) {
  .footerContainer {
    flex-direction: row;
  }

  .row {
    flex-direction: row;
  }

  .row section {
    flex-basis: 33%;
  }

  .footer hr {
    width: 67%;
    margin-left: auto;
    margin-right: 0;
  }
}
